.module001-text-container {
  align-self: stretch;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--gap-16px);
  max-width: 100%;
  text-wrap: wrap;
}

.vrcdjss-icon {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover
}









.module-stacker {
  margin: 0;
  flex: 1;
  position: relative;
  display: inline-block;
  width: 100%;

  a {
    font-size: var(--font-size-24);
    text-decoration: underline;
  }

  a:hover {
    text-decoration: none;
  }

}

.vrcpepss-icon {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover
}

