.aboutus {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-64px);
  box-sizing: border-box;
  gap: 64px;
  max-width: 1600px;
  margin: auto;
}


























@media screen and (max-width: $media-screen-lg) {
  .aboutus {
    padding: var(--padding-32px);
    gap: 48px;
  }
}


@media screen and (max-width: $media-screen-md) {
  .aboutus {
    padding: var(--padding-24px);
    gap: 32px;
  }
  .module001-text-container {
    align-self: stretch;
    display: grid;
    grid-template-columns: 1fr;
    gap: var(--gap-16px);
    max-width: 100%
  }
}

@media screen and (max-width: $media-screen-sm) {
  .aboutus {
    padding: var(--padding-16px);
    gap: 24px;
  }
}